/*
  Description: Contains all the checkboxes for a particular
    category.
*/

import React, { Component } from "react";
import "../../style/filterChecklist.css";

// Presentational Component
import Checkbox from "./checkbox";

import { Scrollbars } from "react-custom-scrollbars";

class FilterChecklist extends Component {
  state = {
    isExpanded: null
  };

  constructor() {
    super();

    this.toggleCollapsible = this.toggleCollapsible.bind(this);
  }

  componentDidMount() {
    this.setState({ isExpanded: this.props.isExpanded });
  }

  toggleCollapsible() {
    this.setState({ isExpanded: !this.state.isExpanded });
  }

  toggleCheckboxChangeStyle() {}

  render() {
    return (
      <div className="collapsible">
        <div
          className="filter-title"
          // id={this.props.category}
          onClick={this.toggleCollapsible}
          style={
            this.state.isExpanded
              ? { backgroundColor: "black" }
              : { backgroundColor: "#e35f20" }
          }
        >
          <h2>{this.props.name}</h2>
          <span>
            {this.state.isExpanded
              ? String.fromCharCode(43)
              : String.fromCharCode(8722)}
          </span>
        </div>
        {!this.state.isExpanded && (
          <Scrollbars style={{ height: 185, width: 400 }}>
            <div className="filter-list" id={this.props.category}>
              {this.props.checkboxes
                .filter(checkbox => checkbox.category === this.props.category)
                .map(checkbox => {
                  return (
                    <Checkbox
                      key={checkbox.id}
                      checkbox={checkbox}
                      handleChange={this.props.handleChange}
                    />
                  );
                })}
            </div>
          </Scrollbars>
        )}
      </div>
    );
  }
}

export default FilterChecklist;



